
.perfil {/* 
    width: 50%; */
    padding-top: 50px;
    color: #FFF;
    background-color: #8841DA;
  }
  .perfil__avatar {
    display: block;
    margin: 0 auto;
    border-radius: 50em;
  }
  .perfil__nombre, .perfil__minibio h2 {
    text-align: center;
    font-size: 25px;
    font-family: "Oswald", sans-serif;
    text-transform: uppercase;
    font-weight: 700;
  }
  .perfil__titulo, .perfil__minibio h3 {
    display: block;
    text-transform: uppercase;
    text-align: center;
    font-weight: 700;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.5);
  }
  .perfil__boton {
    display: block;
    width: 100px;
    height: 40px;
    margin: 15px auto;
    padding-top: 5px;
    border-radius: 20px;
    text-decoration: none;
    text-align: center;
    color: #FFF;
    font-family: "Oswald", sans-serif;
    text-transform: uppercase;
    font-weight: 700;
    border: 2px solid rgba(255, 255, 255, 0.3);
    background-color: rgba(0, 0, 0, 0.1);
  }
  
  .perfil__minibio {
    margin: 0 auto;
    padding-top: 20px;
  }

  
.profile {
  display: grid;
  grid-template-columns: 1fr;
  height: auto;
}

@media screen and (min-width: 880px) {
  
.profile {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
}